.preloader {
  padding: 0;
  position: relative;
  margin: 50px 50px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 60vh;
}
.circlePreloader {
  display: block;
  width: 50px;
  height: 50px;
  border: 4px solid #444;
  border-bottom-color: #888;
  border-radius: 50%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  animation: spin 0.75s infinite linear;
}
.preloaderText {
  max-width: 280px;
  font-family: "Roboto";
  font-weight: 400;
  font-size: 22px;
  line-height: 26px;
  color: grey;
}

@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}
